    --main-bg-color: #384959;
}
body{
    height: 100vh;  
}
.container {
    height: 100%;
    display: grid;

    grid-template-columns: 1fr 2.7fr;
    grid-template-rows: 1fr 5fr;
    background-color: var(--main-bg-color);
}
.upbar {
  grid-column: 1 / 3;
  grid-row: 1/2;
}

.sidebar>.down{
    text-align: center;
}
form{
    display: grid;
}
.dialogBtns{
    gap: 10px;
    justify-content: center;
    display: flex;
}
.dialogBtns>button{
    appearance: none;
    border: none;
    font-size: large;
    color: white;
    background-color: #88BDF2;
}
.dialogBtns>button:hover{
    font-weight: bold;
}
#dialogTodo{
    margin: 0 auto;
    top: 30%;
}
#dialogProj{
 margin: 0;
 padding: 5px;
 opacity: 1;
 /*transform: translateX(-100%); */
 top: 75%;
 left: 5%;
 border: none;
 background-color:  #88BDF2;
}
dialog::backdrop{
    opacity: 0.1;
}
.icon:hover {
  filter: 
    invert(0.5)
    sepia(1)
    hue-rotate(200deg)
    saturate(4)
    brightness(1);
}
.X{
    color: red;
}

h1{
    color: white;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 78px;
    text-align: center;
}
h2{
    text-align: center;
}
.content{
    
    color: white;
    display: flex;
    flex-direction: column;

    align-items: center;
    font-size: large;
}
.top>button , #projectsText, .projectList>*,button{
    background-color: var(--main-bg-color);
    color: #BDDDFC;
    font-family: 'Courier New', Courier, monospace;
    font-size: xx-large;
    font-weight: 500;
    appearance: none;
    border: none;

}
.todoBlock{
    padding: 10px;
    margin-top: 20px;
    width: 80%;
    display: flex;
    border-radius: 2px;
    color: white;
    gap: 20px;
    background-color: #6e7e8d ;
}
button:hover{
    font-weight: bolder;

}
.down{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.add {
    border: none;
    background-color: var(--main-bg-color);
    font-weight: 500;
    font-family: 'Courier New', Courier, monospace;
    color: #88BDF2;
    font-size: 28px;
    display: flex;
    gap: 14px;
}
#projectsText{
    color: white;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,wBAAwB;AAC5B;AACA;IACI,aAAa;AACjB;AACA;IACI,YAAY;IACZ,aAAa;;IAEb,gCAAgC;IAChC,2BAA2B;IAC3B,sCAAsC;AAC1C;AACA;EACE,kBAAkB;EAClB,aAAa;AACf;;AAEA;IACI,kBAAkB;AACtB;AACA;IACI,aAAa;AACjB;AACA;IACI,SAAS;IACT,uBAAuB;IACvB,aAAa;AACjB;AACA;IACI,gBAAgB;IAChB,YAAY;IACZ,gBAAgB;IAChB,YAAY;IACZ,yBAAyB;AAC7B;AACA;IACI,iBAAiB;AACrB;AACA;IACI,cAAc;IACd,QAAQ;AACZ;AACA;CACC,SAAS;CACT,YAAY;CACZ,UAAU;CACV,iCAAiC;CACjC,QAAQ;CACR,QAAQ;CACR,YAAY;CACZ,0BAA0B;AAC3B;AACA;IACI,YAAY;AAChB;AACA;EACE;;;;;iBAKe;AACjB;AACA;IACI,UAAU;AACd;;AAEA;IACI,YAAY;IACZ,qGAAqG;IACrG,eAAe;IACf,kBAAkB;AACtB;AACA;IACI,kBAAkB;AACtB;AACA;;IAEI,YAAY;IACZ,aAAa;IACb,sBAAsB;;IAEtB,mBAAmB;IACnB,gBAAgB;AACpB;AACA;IACI,sCAAsC;IACtC,cAAc;IACd,8CAA8C;IAC9C,mBAAmB;IACnB,gBAAgB;IAChB,gBAAgB;IAChB,YAAY;;AAEhB;AACA;IACI,aAAa;IACb,gBAAgB;IAChB,UAAU;IACV,aAAa;IACb,kBAAkB;IAClB,YAAY;IACZ,SAAS;IACT,0BAA0B;AAC9B;AACA;IACI,mBAAmB;;AAEvB;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;AACA;IACI,YAAY;IACZ,sCAAsC;IACtC,gBAAgB;IAChB,8CAA8C;IAC9C,cAAc;IACd,eAAe;IACf,aAAa;IACb,SAAS;AACb;AACA;IACI,YAAY;AAChB","sourcesContent":[":root{\n    --main-bg-color: #384959;\n}\nbody{\n    height: 100vh;  \n}\n.container {\n    height: 100%;\n    display: grid;\n\n    grid-template-columns: 1fr 2.7fr;\n    grid-template-rows: 1fr 5fr;\n    background-color: var(--main-bg-color);\n}\n.upbar {\n  grid-column: 1 / 3;\n  grid-row: 1/2;\n}\n\n.sidebar>.down{\n    text-align: center;\n}\nform{\n    display: grid;\n}\n.dialogBtns{\n    gap: 10px;\n    justify-content: center;\n    display: flex;\n}\n.dialogBtns>button{\n    appearance: none;\n    border: none;\n    font-size: large;\n    color: white;\n    background-color: #88BDF2;\n}\n.dialogBtns>button:hover{\n    font-weight: bold;\n}\n#dialogTodo{\n    margin: 0 auto;\n    top: 30%;\n}\n#dialogProj{\n margin: 0;\n padding: 5px;\n opacity: 1;\n /*transform: translateX(-100%); */\n top: 75%;\n left: 5%;\n border: 